import { Component, OnInit, Inject } from "@angular/core";
import { MatDialogRef, MAT_DIALOG_DATA, MatDialog } from "@angular/material";
import { RegisterDialogComponent } from "../register-dialog/register-dialog.component";
import { AppService } from "../app.service";

@Component({
  selector: "app-login-dialog",
  template: `
  <form [style.display]="this.appService.userState == null ? 'flex' : 'none' " style="width: 300px; flex-direction: column;">

    <mat-icon style="align-self: center; width: 40px;height: 40px;font-size: 40px">account_circle</mat-icon>
    <div style="align-self: center; margin-top: 10px;">Sign In</div>
    <mat-form-field>
      <input [(ngModel)]="phoneNumber" [ngModelOptions]="{standalone: true}" matInput placeholder="Phone Number">
    </mat-form-field>

    <mat-form-field>
      <input [(ngModel)]="password" [ngModelOptions]="{standalone: true}" matInput [type]="hide ? 'password' : 'text'" placeholder="Password" />
      <mat-icon matSuffix (click)="hide = !hide">{{hide ? 'visibility' : 'visibility_off'}}</mat-icon>
    </mat-form-field>
    
    <button mat-raised-button color="primary" (click)="onSignInClick()">Sign In</button>

    <p> New? <a style="cursor: pointer" color="primary" (click)="this.onSignUpClick()">Sign up now!</a> </p>
    
  </form>

  <div [style.display]="this.appService.userState != null ? 'flex' : 'none' " style="width: 300px; flex-direction: column;">
    <p> Hi {{this.appService.userState?.username}}, do you want to logout?</p>
    <button mat-raised-button (click)="onLogoutClick()">Logout!</button>
  </div>
  `,
  styles: []
})
export class LoginDialogComponent implements OnInit {
  public hide: boolean = true;
  public phoneNumber: string;
  public password: string;

  constructor(
    public dialog: MatDialog,
    public dialogRef: MatDialogRef<LoginDialogComponent>,
    public appService: AppService,
    @Inject(MAT_DIALOG_DATA) public data: any
  ) {}
  ngOnInit(): void {}

  onSignUpClick(): void {
    this.dialogRef.close();
    this.dialog.open(RegisterDialogComponent);
  }
  onLogoutClick(): void {
    this.appService.userState = null;
  }
  onSignInClick(): void {
    let userInfo = this.appService.uesrLogin(this.phoneNumber, this.password);
    if (null != userInfo) {
      this.appService.userState = userInfo;
      this.dialogRef.close();
    }
  }
}



