import { Component, OnInit, Inject } from "@angular/core";
import { MatDialogRef, MAT_DIALOG_DATA } from "@angular/material";

@Component({
  selector: "app-login-dialog",
  template: `
  <form style="width: 300px; display: flex; flex-direction: column;">

    <mat-icon style="align-self: center; width: 40px;height: 40px;font-size: 40px">account_circle</mat-icon>
    <div style="align-self: center; margin-top: 10px;">Sign Up</div>
    <mat-form-field>
      <input matInput placeholder="Username">
    </mat-form-field>
    <mat-form-field>
      <input matInput placeholder="Phone Number">
    </mat-form-field>

    <mat-form-field>
      <input  matInput [type]="hide ? 'password' : 'text'" placeholder="Password" />
      <mat-icon matSuffix (click)="hide = !hide">{{hide ? 'visibility' : 'visibility_off'}}</mat-icon>
    </mat-form-field>
    
    <mat-form-field>
      <input  matInput [type]="hide ? 'password' : 'text'" placeholder="Confirm Password" />
      <mat-icon matSuffix (click)="hide = !hide">{{hide ? 'visibility' : 'visibility_off'}}</mat-icon>
    </mat-form-field>
    <button mat-raised-button color="primary">Sign Up</button>
    
  </form>
  `,
  styles: []
})
export class RegisterDialogComponent implements OnInit {
  public hide: boolean = true;
  constructor(
    public dialogRef: MatDialogRef<RegisterDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any
  ) {}
  ngOnInit(): void {}
}



