import { Component, OnInit, Inject } from "@angular/core";
import { MAT_DIALOG_DATA, MatDialogRef } from "@angular/material";
import { Book, Review, Library } from "../entities";

@Component({
  selector: "app-book-dialog",
  template: `
    <div class="book-dialog-container">
      <div class="section">
        <div class="item-row">
          <h2 class="item-label">书名：{{data.name}}</h2>
        </div>
        <div class="item-row">
          <div class="item-label">作者：{{data.author}} </div>
          <div class="item-label">类别：{{data.catagory}}</div>
        </div>
        <mat-form-field>
          <mat-select #selectedLib placeholder="图书馆">
            <mat-option value="default">Default</mat-option>
            <mat-option *ngFor="let lib of libs" [value]="lib.id">
              {{ lib.address }}
            </mat-option>
          </mat-select>
        </mat-form-field>

        <button mat-raised-button color="primary" (click)="onReserveClick(selectedLib.value)">预定</button>

        <mat-list role="list">
          <mat-list-item *ngFor="let lib of libs">
            {{lib.area}}-{{lib.address}} <button mat-button (click)="onBorrowClick(lib.id)">借阅</button>
          </mat-list-item>
        </mat-list>

      </div>
      <div class="section">
        <mat-list>
          <h3 mat-subheader>评论</h3>
          <mat-list-item *ngFor="let note of notes">
            <mat-icon mat-list-icon>note</mat-icon>
            <p mat-line> {{note.content}} </p>
          </mat-list-item>
        </mat-list>
        <mat-form-field>
          <textarea #commentBox matInput placeholder="说点什么吧..."></textarea>
        </mat-form-field>
        <button mat-raised-button color="primary" (click)="onCommentClick(commentBox.value)">评论</button>
      </div>
      
    </div>
  `,
  styles: [
    `
    .book-dialog-container{
      display: flex;
      min-width: 600px;
    }
    .book-dialog-container .section{
      display: flex;
      flex: 1;
      flex-direction: column;
      align-items:stretch;
      padding: 0 10px;
    }
    .item-row{
      margin-bottom: 10px;
      display: flex;
    }
    .item-row .item-label{
      flex: 3;
    }
    .item-row .item-button{
      flex: 1;
    }
  `
  ]
})
export class BookDialogComponent implements OnInit {
  notes: Review[] = [
    Object.assign({ name: "", author: "", content: "好书推荐", accountId: 0 }),
    Object.assign({ name: "", author: "", content: "好书推荐2", accountId: 0 }),
    Object.assign({ name: "", author: "", content: "好书推荐3", accountId: 0 })
  ];
  libs: Library[] = [
    Object.assign({ id: 2, area: "温江区", address: "柳台路555号" }),
    Object.assign({ id: 1, area: "青羊区", address: "红星路17号" })
  ];

  constructor(
    public dialogRef: MatDialogRef<BookDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: Book
  ) {}

  ngOnInit() {}

  onReserveClick(libId: string) {
    console.log(libId);
  }

  onBorrowClick(libId: string) {
    console.log(libId);
  }

  onCommentClick(comment: string) {
    console.log(comment);
  }
}



