import { Component, OnInit } from "@angular/core";
import { MatIconRegistry, MatDialog } from "@angular/material";
import { DomSanitizer } from "@angular/platform-browser";
import { AppService } from "../app.service";
import { Book } from "../entities";
import { BookDialogComponent } from "../book-dialog/book-dialog.component";

@Component({
  selector: "app-search",
  templateUrl: "./search.component.html",
  styleUrls: ["./search.component.scss"]
})
export class SearchComponent implements OnInit {
  constructor(private appService: AppService, public dialog: MatDialog) {}

  public searchResults: Array<Book> = null;
  public searchText: string = "";
  public isSearching: boolean = false;

  ngOnInit() {}

  onKeyDown(event: KeyboardEvent): void {
    if (event.key === "Enter") {
      event.preventDefault();
      let inputText = (event.target as HTMLElement).innerText.trim();
      if (inputText === "") return;
      console.log(inputText);

      this.searchText = inputText;
      this.isSearching = true;

      setTimeout(() => {
        this.appService.search(inputText).then(result => {
          this.searchResults = result;
          this.isSearching = false;
        });
      }, 1000);
    }
  }
  onBookClick(book: Book): void {
    let dialogRef = this.dialog.open(BookDialogComponent, { data: book });
  }
}



