import { Injectable } from "@angular/core";
import { Book, Account } from "./entities";
import {
  CanActivate,
  ActivatedRouteSnapshot,
  RouterStateSnapshot
} from "@angular/router";
import { Observable } from "rxjs/Observable";
import { MatDialog } from "@angular/material";
import { LoginDialogComponent } from "./login-dialog/login-dialog.component";

@Injectable()
export class AppService {
  constructor(public dialog: MatDialog) {}

  private userStateValue: Account;

  get userState(): Account {
    if (this.userStateValue == null) {
      let str = sessionStorage.getItem("userState");
      if (str != null && str !== "") this.userStateValue = JSON.parse(str);
    }
    return this.userStateValue;
  }

  set userState(newState: Account) {
    sessionStorage.setItem("userState", JSON.stringify(newState));
    this.userStateValue = newState;
  }

  uesrLogin(phoneNumber: string, password: string): Account {
    if (phoneNumber == "admin" && password == "admin") {
      let adminAcct = new Account();
      adminAcct.id = 0;
      adminAcct.username = "admin";
      adminAcct.password = "admin";
      adminAcct.phone = "95588";
      adminAcct.isDeposit = false;
      return adminAcct;
    }

    return null;
  }

  userRegister(
    username: string,
    password: string,
    phoneNumber: string
  ): Account {
    return null;
  }

  search(searchText: string): Promise<Array<Book>> {
    // test scope:
    let book1 = new Book();
    book1.catagory = "神奇小说";
    book1.id = 1;
    book1.isBorrow = false;
    book1.isReserve = false;
    book1.libraryId = 1;
    book1.name = "哈利波特";
    book1.author = "JK 罗琳";

    let book2 = Object.create(book1);
    let book3 = Object.create(book1);
    let book4 = Object.create(book1);
    let book5 = Object.create(book1);

    return new Promise(invoker => invoker([book1, book2, book3, book4, book5]));
  }
}



