export class Account {
  public id: number;
  public username: string;
  public password: string;
  public phone: string;
  public balance: number;
  public isDeposit: boolean;
}

export class Book {
  public id: number;
  public name: string;
  public author: string;
  public libraryId: number;
  public catagory: string;
  public isBorrow: boolean;
  public isReserve: boolean;
}

export class Library {
  public id: number;
  public area: string;
  public address: string;
}

export interface IBookRecord {
  accountId: number;
  isBorrow: boolean;
  bookName: string;
  libraryName: string;
  date: Date;
}

export class BookRecord implements IBookRecord {
  public accountId: number;
  public isBorrow: boolean;
  public bookId: number;
  public bookName: string;
  public libraryId: number;
  public libraryName: string;
  public date: Date;
}

export interface IPayRecord {
  accountId: number;
  isSave: boolean;
  amount: number;
  balance: number;
  date: Date;
  detail: string;
}

export class PayRecord implements IPayRecord {
  public accountId: number;
  public isSave: boolean;
  public amount: number;
  public balance: number;
  public date: Date;
  public detail: string;
}

export class Reserve {
  public accountId: number;
  public name: string;
  public author: string;
  public libraryId: string;
  public date: Date;
  public reserveDate: Date;
}

export class WishList {
  public name: string;
  public author: string;
  public publisher: string;
}

export class Review {
  public name: string;
  public author: string;
  public content: string;
  public accountId: number;
}

export class Catagory {
  public sort: string;
  public id: string;
}



