import { Component, OnInit, ViewChild } from "@angular/core";
import { AppService } from "../app.service";
import {
  OnDestroy,
  AfterContentInit,
  AfterViewChecked,
  AfterViewInit
} from "@angular/core/src/metadata/lifecycle_hooks";
import { MatDialog, MatTableDataSource, MatSort } from "@angular/material";
import { LoginDialogComponent } from "../login-dialog/login-dialog.component";
import { BookRecord, IBookRecord } from "../entities";
import { READING_DATA, PAYMENT_DATA } from "./mockdata";

@Component({
  selector: "app-profile",
  templateUrl: "./profile.component.html",
  styleUrls: ["./profile.component.scss"]
})
export class ProfileComponent implements OnInit, AfterViewInit {
  constructor(public appService: AppService, public dialog: MatDialog) {}

  public displayedColumns = ["书名", "借还状态", "图书馆", "日期"];
  public dataSource = new MatTableDataSource(READING_DATA);

  public displayedColumns2 = ["isSave", "amount", "balance", "detail", "date"];
  public dataSource2 = new MatTableDataSource(PAYMENT_DATA);

  @ViewChild(MatSort) sort: MatSort;

  /**
   * Set the sort after the view init since this component will
   * be able to query its view for the initialized sort.
   */
  ngAfterViewInit() {
    this.dataSource.sort = this.sort;
    this.dataSource2.sort = this.sort;
  }
  ngOnInit() {}
}



